import React from 'react'

type NoteProps = {
  note: {
    id: number,
    content: string,
    important: boolean,
    date: string
  }
  toggleImportance: any
}
const Note: React.FC<NoteProps> = ({ note, toggleImportance }) => {
  const label = note.important
    ? 'make not important' : 'make important'

  return (
    <li className='note'>
      {note.content}
      <button onClick={toggleImportance}>{label}</button>
    </li>
  )
}

export default Note